<template>
    <div class="single-card">
        <v-card class="infoCard" :class="cardData.state">
            <v-card-title class="cardTitle">
                <v-row>
                    <v-col cols="12">
                        {{ generateFileName(cardData.parentFile.fullFile) }}
                    </v-col>
                </v-row>
            </v-card-title>
            <v-card-text class="cardText">
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Suite Description</span>
                    </v-col>
                    <!-- <v-divider vertical></v-divider> -->
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.parentSuite.title }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Test Description</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.onlyTitle }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Fixture File</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.fixtureFile }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Status</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.statusCode }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Method</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.method }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Result</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.state }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Response Type</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.responseContentType }}
                        </span>
                    </v-col>
                </v-row>
                <v-divider class="my-3"></v-divider>
                <v-row>
                    <v-col cols="3">
                        <span class="font-weight-black cardTextTitle">Request Type</span>
                    </v-col>
                    <v-col cols="9">
                        <span class="font-weight-medium cardTextDesc">
                            {{ cardData.requestContentType }}
                        </span>
                    </v-col>
                </v-row>
            </v-card-text>
            <v-card-actions class="justify-end">
                <v-btn outlined color="white" @click="showCodePopup = true">
                    <b>Code</b>
                </v-btn>
                <v-btn v-if="cardData.fail" outlined color="white" @click="showErrorPopup = true">
                    <b>Error</b>
                </v-btn>
            </v-card-actions>
        </v-card>

        <v-dialog v-model="showCodePopup" width="1000" max-width="1000">
            <v-card>
                <v-card-title class="text-h5">
                    <v-row>
                        <v-col cols="11">
                            <h2>Code</h2>
                        </v-col>
                        <v-col cols="1" class="text-end">
                            <v-btn @click="showCodePopup = false" icon>
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>

                </v-card-title>
                <v-divider></v-divider>
                <v-card-text class="dialogCardText">
                    <pre>
                        {{ `\n${cardData.code.trim()}` }}
                    </pre>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-dialog v-model="showErrorPopup" width="1000" max-width="1000">
            <v-card>
                <v-card-title class="text-h5">
                    <v-row>
                        <v-col cols="11">
                            <h2>Error Details</h2>
                        </v-col>
                        <v-col cols="1" class="text-end">
                            <v-btn @click="showErrorPopup = false" icon>
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>

                </v-card-title>
                <v-divider></v-divider>
                <v-card-text class="mt-5 dialogCardText">
                    <v-row>
                        <v-col cols="2">
                            <h3>diff</h3>
                        </v-col>
                        <v-col cols="10">
                            <div>
                                {{ cardData.err.diff }}
                            </div>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="2">
                            <h3>estack</h3>
                        </v-col>
                        <v-col cols="10">
                            <div>
                                {{ cardData.err.estack }}
                            </div>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="2">
                            <h3>message</h3>
                        </v-col>
                        <v-col cols="10">
                            <div>
                                {{ cardData.err.message }}
                            </div>
                        </v-col>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
export default {
    name: 'InfoCard',
    props: ['cardData'],
    data() {
        return {
            showCodePopup: false,
            showErrorPopup: false,
        }
    },
    mounted() {


    },
    methods: {
        generateFileName(fileFullPath) {
            const parts = fileFullPath.split("\\");
            const name = parts[parts.length - 1];
            return name.replace(".cy.js", "");
        },
    }
}
</script>

<style>
.single-card {
    width: 33%;
    padding: 12px;
    transition: all 0.3s ease-in-out;
}

.single-card:hover {
    transform: scale(1.03);
}

.infoCard {
    /* height: 100%; */
    box-shadow: rgba(0, 0, 0, 0.56) 2px 3px 13px 3px !important;
}

.infoCard.passed {
    /* background-image: linear-gradient(295deg, #4caf50 20%, transparent 190%); */
    background-color: #65b862 !important;
}

.infoCard.failed {
    /* background-image: linear-gradient(295deg, #f44336 20%, transparent 190%); */
    background-color: #e25151 !important;
}

.infoCard.skipped {
    /* background-image: linear-gradient(295deg, #989898 20%, transparent 190%); */
    background-color: #989898 !important;
}

.infoCard.pending {
    /* background-image: linear-gradient(295deg, #f6a83a 20%, transparent 190%); */
    background-color: #da763d !important;
}

.cardTitle {
    color: white;
    font-weight: bold !important;
    background-color: #0000004d;
    padding: 12px !important;
    margin-bottom: 20px;
}

.cardText {
    line-height: 1.2 !important;
    /* color: #1c1c1c !important; */
    color: white !important;
}

.cardTextTitle {
    font-family: monospace;
}
.cardTextDesc {
    font-family: monospace;
}

.dialogCardText {
    color: black !important;
}

.text-shadow {
    /* text-shadow: 2px 2px 4px black, -2px -2px 4px black; */
    text-shadow: 2px 1px 2px black;
}

/* --------------------------------------- */
@media (max-width: 991px) {
    .single-card {
        width: 50%;
    }
}

@media (max-width: 767px) {
    .single-card {
        width: 80%;
    }
}

@media (max-width: 600px) {
    .single-card {
        width: 100%;
    }
}

/* @media (max-width: 478px) {
    .single-card {
        width: 100%;
    }
} */

/* --------------------------------------- */

@keyframes onCardHover {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.03);
    }
}
</style>